import { Entity } from '../entity';
import { Depth } from '../flags';
import * as Kind from './kind';
export class Item extends Entity {
    constructor(kind) {
        super(kind);
        this.quantity = 1;
        this.next = null;
        // @ts-ignore
        this.flags = this.flags || {};
        this.flags.item = 0;
        this.depth = Depth.ITEM;
        this.kind = kind;
    }
    itemFlags() {
        return this.flags.item;
    }
    hasItemFlag(flag) {
        return !!(this.flags.item & flag);
    }
    hasAllItemFlags(flags) {
        return (this.flags.item & flags) === flags;
    }
}
export function make(id) {
    const kind = Kind.get(id);
    if (!kind)
        throw new Error('Failed to find item kind - ' + id);
    return new Item(kind);
}
export function makeRandom(opts) {
    const kind = Kind.randomKind(opts);
    if (!kind)
        throw new Error('Failed to find item kind matching - ' + JSON.stringify(opts));
    return new Item(kind);
}
export function from(info) {
    let kind;
    if (typeof info === 'string') {
        // @ts-ignore
        kind = Kind.get(info);
        if (!kind)
            throw new Error('Failed to find item kind - ' + info);
    }
    else if (info instanceof Kind.ItemKind) {
        kind = info;
    }
    else {
        kind = Kind.makeKind(info);
    }
    return new Item(kind);
}
