import * as GWU from 'gw-utils';
import * as EFFECT from '../install';
//////////////////////////////////////////////
// EMIT
export class EmitEffect {
    make(src, dest) {
        if (!src.emit)
            return true;
        if (typeof src.emit !== 'string') {
            throw new Error('emit effects must be string name to emit: { emit: "EVENT" }');
        }
        dest.emit = src.emit;
        return true;
    }
    async fire(config, _map, x, y, ctx) {
        if (config.emit) {
            await GWU.events.emit(config.emit, x, y, ctx);
            return true;
        }
        return false;
    }
    fireSync(config, _map, _x, _y, _ctx) {
        if (!config.emit)
            return false;
        throw new Error('Cannot use "emit" effects in build steps.');
    }
}
EFFECT.installHandler('emit', new EmitEffect());
