import * as EFFECT from '../install';
import * as FIRE from '../fire';
//////////////////////////////////////////////
// EMIT
export class EffectEffect {
    make(src, dest) {
        if (!src.effect)
            return true;
        dest.effect = src.effect;
        return true;
    }
    async fire(config, map, x, y, ctx) {
        if (config.effect) {
            return await FIRE.fire(config.effect, map, x, y, ctx);
        }
        return false;
    }
    fireSync(config, map, x, y, ctx) {
        if (config.effect) {
            return FIRE.fireSync(config.effect, map, x, y, ctx);
        }
        return false;
    }
}
EFFECT.installHandler('effect', new EffectEffect());
